//////////////////////// bootstrap icon ////////////////////////
import '../node_modules/bootstrap-icons/font/bootstrap-icons.css';
//////////////////////// custom style ////////////////////////
import './style/style.scss';

///////////////////////////// Info Output /////////////////////////////////
import './js/info';
import './js/dom';

const hireBtn = document.querySelector('#hire');
const hireModal = document.querySelector('#hire-modal');

hireBtn.addEventListener('click', () => {
    hireModal.style.display = 'flex';
});

const closeHireBtn = document.querySelector('#close-hire');
closeHireBtn.addEventListener('click', () => {
    hireModal.style.display = 'none';
});

const moreInfo = document.querySelector('#more-in');
const infos = document.querySelector('#info');
moreInfo.addEventListener('click', () => {
    window.scroll(0, infos.offsetTop);
});
