,kBAAA;ED4bN;ECxbE;IACI,oBAAA;IACA,kBAAA;ED0bN;ECtbE;IACI,kBAAA;EDwbN;ECtbE;IACI,iBAAA;EDwbN;ECrbE;IACI,4BAAA;IACA,aAAA;EDubN;ECtbM;IACI,gBAAA;EDwbV;ECpbE;IACI,aAAA;IACA,2BAAA;EDsbN;ECnbE;IACI,cAAA;EDqbN;EClbE;IACI,4BAAA;EDobN;EClbU;IACI,cAAA;EDobd;EChbE;IACI;MACI,4BAAA;IDkbR;IChbI;MACI,uBAAA;IDkbR;EACF;EC/aE;IACI;MACI,2BAAA;IDibR;IC/aI;MACI,uBAAA;IDibR;EACF;EC9aE;IACI,kBAAA;IACA,iBAAA;EDgbN;EC9aU;IACI,kBAAA;EDgbd;EC/ac;IACI,kBAAA;IACA,WAAA;IACA,UAAA;IACA,MAAA;IACA,SAAA;EDiblB;EC/ac;IACI,kBAAA;IACA,WAAA;IACA,UAAA;IACA,SAAA;IACA,UAAA;EDiblB;EC5aE;IAEI,eAAA;IACA,YAAA;IAEA,cAAA;ED4aN;EC1aM;IACI,sBAAA;IACA,uBAAA;ED4aV;ECxaE;IACI,cAAA;ED0aN;AACF","sourcesContent":["// containers\r\n.container-fluid {\r\n    width: 95%;\r\n    margin: auto;\r\n}\r\n\r\n.container {\r\n    max-width: 1440px;\r\n    margin: auto;\r\n    padding: 0 1rem;\r\n}\r\n","$semi-dark: #333;\r\n$secondary: #ccc;\r\n$cool-v: rgb(194, 10, 201);\r\n$border-v: #9852f9;\r\n$light-blue: #00bbf0;\r\n\r\n@import './utilities/utilities';\r\n\r\n*,\r\n::before,\r\n::after {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    scroll-behavior: smooth;\r\n}\r\n\r\nbody {\r\n    background-color: white;\r\n    font-size: 1em;\r\n    line-height: 1.5;\r\n    font-family: Arial, Helvetica, sans-serif;\r\n    overflow-x: hidden;\r\n}\r\na {\r\n    text-decoration: none;\r\n    color: unset;\r\n}\r\n\r\n///////////////////// opening style //////////////////////////\r\n.opening {\r\n    overflow: hidden;\r\n    height: 100vh;\r\n    width: 100%;\r\n    position: relative;\r\n    background-color: $secondary;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    animation: fadeOut 300ms forwards 3000ms;\r\n    z-index: 20;\r\n    position: fixed;\r\n    top: 0;\r\n    // opening image animation\r\n    #opening-image {\r\n        z-index: 20;\r\n        border-radius: 50%;\r\n        animation: trans 1s forwards, bord 400ms forwards 1s;\r\n    }\r\n    @keyframes bord {\r\n        0% {\r\n            border: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border: 10px solid $cool-v;\r\n        }\r\n    }\r\n    @keyframes trans {\r\n        0% {\r\n            transform: scale(1);\r\n        }\r\n        100% {\r\n            transform: scale(0.8);\r\n        }\r\n    }\r\n\r\n    // before animation\r\n    &::before {\r\n        content: '';\r\n        width: 50%;\r\n        height: 100vh;\r\n        position: absolute;\r\n        left: 0;\r\n        background-color: $semi-dark;\r\n        animation: beforeB 200ms forwards 1500ms, slideLeft 1s forwards 2000ms;\r\n    }\r\n    @keyframes beforeB {\r\n        0% {\r\n            border-right: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border-right: 5px solid $cool-v;\r\n        }\r\n    }\r\n    @keyframes slideLeft {\r\n        0% {\r\n            transform: translate(0);\r\n        }\r\n        100% {\r\n            transform: translate(-100%);\r\n        }\r\n    }\r\n\r\n    // after animation\r\n    &::after {\r\n        content: '';\r\n        width: 50%;\r\n        height: 100vh;\r\n        position: absolute;\r\n        right: 0;\r\n        background-color: $semi-dark;\r\n        animation: afterB 200ms forwards 1500ms, slideRight 1s forwards 2000ms;\r\n    }\r\n    @keyframes slideRight {\r\n        0% {\r\n            transform: translate(0);\r\n        }\r\n        100% {\r\n            transform: translate(100%);\r\n        }\r\n    }\r\n    @keyframes afterB {\r\n        0% {\r\n            border-left: 0px solid $cool-v;\r\n        }\r\n        100% {\r\n            border-left: 5px solid $cool-v;\r\n        }\r\n    }\r\n}\r\n@keyframes fadeOut {\r\n    0% {\r\n        opacity: 1;\r\n        display: unset;\r\n    }\r\n    100% {\r\n        opacity: 0;\r\n        display: none;\r\n        position: absolute;\r\n        z-index: -2;\r\n    }\r\n}\r\n\r\n///////////////////// nav bar //////////////////////////\r\nnav {\r\n    background-color: $border-v;\r\n    color: white;\r\n    border-bottom: $cool-v 3px solid;\r\n    padding: 5px 0;\r\n    position: fixed;\r\n    top: 0;\r\n    width: 100%;\r\n    z-index: 10;\r\n\r\n    .nav-flexer {\r\n        .nav-brand {\r\n            // display: none;\r\n            font-size: 1.6em;\r\n            font-weight: bold;\r\n        }\r\n\r\n        .nav-list {\r\n            .nav-items {\r\n                display: flex;\r\n                justify-content: space-evenly;\r\n                align-items: flex-start;\r\n                margin-top: 1rem;\r\n                li {\r\n                    list-style: none;\r\n                    a {\r\n                        font-size: 1.2em;\r\n                        transition: color 300ms;\r\n                    }\r\n\r\n                    a:hover {\r\n                        color: $light-blue;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// /////////////////////////profile section\r\n.profile-section {\r\n    min-height: 100vh;\r\n    margin-top: 7rem;\r\n    background: linear-gradient(to right top, darken($border-v, 50%), $border-v);\r\n    position: relative;\r\n    padding-bottom: 1.5rem;\r\n\r\n    svg {\r\n        // transform: rotate(180deg);\r\n        position: absolute;\r\n        top: 0;\r\n        fill: white;\r\n        stroke: none;\r\n    }\r\n\r\n    .pic-name-pos {\r\n        text-align: center;\r\n        padding-top: 5rem;\r\n        img {\r\n            width: 200px;\r\n            height: 200px;\r\n            border-radius: 50%;\r\n            border: 5px solid lighten($cool-v, 20%);\r\n        }\r\n\r\n        #name {\r\n            font-size: 2.5em;\r\n            font-weight: bold;\r\n            color: white;\r\n        }\r\n        #position {\r\n            color: $light-blue;\r\n            font-size: 1.2em;\r\n            font-weight: bold;\r\n        }\r\n    }\r\n\r\n    .self-descrip {\r\n        max-width: 800px;\r\n        text-align: center;\r\n        padding: 0 2rem;\r\n        margin: 3rem auto;\r\n        color: white;\r\n        line-height: 1.5;\r\n    }\r\n\r\n    .hire-btn {\r\n        display: flex;\r\n        justify-content: center;\r\n        margin-top: 2rem;\r\n        gap: 2rem;\r\n\r\n        button {\r\n            width: 100px;\r\n            padding: 7px 10px;\r\n            font-size: 1em;\r\n            border-radius: 5px;\r\n            border: 1px solid $light-blue;\r\n            background: none;\r\n            color: white;\r\n            transition: background 400ms, background-color 400ms;\r\n        }\r\n        #more-in {\r\n            border: $cool-v 1px solid;\r\n        }\r\n        #more-in:hover {\r\n            background-color: $cool-v;\r\n        }\r\n        #hire:hover {\r\n            background-color: $light-blue;\r\n        }\r\n\r\n        #hire:hover {\r\n            background-color: $light-blue;\r\n        }\r\n    }\r\n}\r\n////////////////////////////////skills\r\n.skills {\r\n    min-height: 100vh;\r\n    background-color: darken($light-blue, 5%);\r\n    position: relative;\r\n\r\n    .curve-opacity {\r\n        svg {\r\n            z-index: 1;\r\n            position: absolute;\r\n            bottom: 0;\r\n            transform: rotate(180deg);\r\n            fill: $cool-v;\r\n        }\r\n    }\r\n\r\n    header {\r\n        h1 {\r\n            color: white;\r\n            font-size: 2.7em;\r\n            padding: 1rem;\r\n        }\r\n    }\r\n\r\n    .skill-grid {\r\n        display: flex;\r\n        justify-content: start;\r\n        flex-wrap: wrap;\r\n\r\n        .skill-item {\r\n            height: 200px;\r\n            width: 200px;\r\n            box-shadow: inset 5px 5px 2px gray, inset 5px 5px 2px white;\r\n            margin: auto;\r\n            margin-bottom: 2rem;\r\n            position: relative;\r\n            border-radius: 50%;\r\n\r\n            display: flex;\r\n            align-items: center;\r\