/////////////// section2 (Skill Section) /////////////////
const getPercent = num => {
    let x = 100 - num;
    return (x / 100) * 472;
};

class Skill {
    constructor(id, per) {
        this.id = id;
        this.per = per;
    }
    anim() {
        const targ = document.querySelector(`#${this.id}`);
        setTimeout(() => (targ.style.strokeDashoffset = getPercent(this.per)), 500);
    }
}

const skillFlexer = document.querySelector('.skill-grid');
const sectionTwo = document.querySelector('.skills');

let ids = [];
myProfile.sectionTwo.forEach(skill => {
    const div = document.createElement('div');
    div.className = 'skill-item';
    div.innerHTML = `
    <div class="progress-bar">
        <svg height="200" width="200">
            <circle id="${skill.id}" r="75" cy="100" cx="100" />
        </svg>
    </div>
    <div>
        <p class="skill-name">${skill.skill}</p>
        <p>exp: ${skill.exp} year(s)</p>
    </div>
    `;

    skillFlexer.append(div);

    ids.push(new Skill(skill.id, skill.progress));
});

window.addEventListener('scroll', e => {
    if (window.scrollY + 500 >= sectionTwo.offsetTop) {
        ids.forEach(sk => {
            new Skill(sk.id, sk.per).anim();
        });
    }
});

